@import "../../variables";

.footer {
	padding-top: 50px;
	padding-bottom: 50px;
	background-color: $neutral-secondary;

	&__inner {
		display: grid;
		grid-template-rows: 1fr 1fr;
		@media (min-width: $desktop) {
			position: relative;
			width: 1100px;
			margin: 0 auto;
		}
	}
}
